  /* ProfileEdit Style */
  .web-profile-button {
    all: unset;
    background-color: #267bf1;
    border-radius: 10px;
    box-sizing: border-box;
    height: 37px;
    position: relative;
    width: 108px;
  }
  
  .web-profile-button .text-wrapper {
    color: #ffffff;
    font-family: "Inter", Helvetica;
    font-size: 16px;
    font-weight: 600;
    /* left: 15px; */
    letter-spacing: 0;
    line-height: 24px;
    position: absolute;
    text-align: center;
    top: 6px;
    white-space: nowrap;
  }
  
  /* Logout Style */
  .web-profile-frame {
    align-items: center;
    display: inline-flex;
    gap: 10px;
    position: relative;
  }
  
  .web-profile-frame .div {
    color: #267bf1;
    font-family: "Inter", Helvetica;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 24px;
    margin-top: -1.00px;
    position: relative;
    text-decoration: underline;
    white-space: nowrap;
    width: fit-content;
  }

  /* Menu Style */
  .web-profile-menu {
    height: 1525px;
    width: 397px;
  }
  
  .web-profile-menu .overlap-group {
    background-color: #ffffff;
    border-color: #e8eaee;
    border-right-style: solid;
    border-right-width: 2px;
    height: 1504px;
    left: -2px;
    position: relative;
    top: -2px;
    width: 401px;
  }
  
  .web-profile-menu .div-2 {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    gap: 32px;
    left: 52px;
    position: absolute;
    top: 174px;
    width: 249px;
  }
  
  .web-profile-menu .home-tab-instance {
    align-self: stretch !important;
    width: 100% !important;
  }
  
  .web-profile-menu .title {
    color: var(--bananistyle);
    font-family: "Inter", Helvetica;
    font-size: 48px;
    font-weight: 800;
    left: 52px;
    letter-spacing: 0;
    line-height: 40px;
    position: absolute;
    top: 81px;
    width: 280px;
  }
  
  /* ToggleButton Style */
  .state-off-wrapper {
    height: 31px;
    width: 62px;
  }
  
  .state-off-wrapper .class {
    background-color: #267bf1 !important;
    width: unset !important;
  }
  
  .state-off-wrapper .class-2 {
    width: unset !important;
  }
  
  .state-off-wrapper .class-3 {
    left: 33px !important;
  }
  
  /* Toggle Style */
  .toggle {
    border-radius: 100px;
    height: 31px;
    overflow: hidden;
    width: 51px;
  }
  
  .toggle .knob {
    background-color: #ffffff;
    border-radius: 100px;
    box-shadow: 0px 3px 1px #0000000f, 0px 3px 8px #00000026, 0px 0px 0px 1px
      #0000000a;
    height: 27px;
    position: relative;
    top: 2px;
    width: 27px;
  }
  
  .toggle.off {
    background-color: var(--system-colors-fills-secondary);
  }
  
  .toggle.on {
    background-color: var(--system-colors-colors-green);
  }
  
  .toggle.off .knob {
    left: 2px;
  }
  
  .toggle.on .knob {
    left: 22px;
  }

  /* User */
  .web-profile-user {
    height: 24px;
    left: 0;
    position: absolute;
    top: 0;
    width: 24px;
  }
  