/* ReadOriginalButton Style */
.web-summary-button {
  position: relative;
}

.web-summary-button .text-wrapper {
  margin-top: -1px;
  position: relative;
  text-align: center;
  white-space: nowrap;
  width: fit-content;
}

.web-summary-button .label {
  margin-top: -1px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.web-summary-button.text {
  align-items: center;
  display: inline-flex;
  justify-content: center;
}

.web-summary-button.symbol-text {
  align-items: center;
  display: inline-flex;
  justify-content: center;
}

.web-summary-button.symbol {
  border-radius: 500px;
}

.web-summary-button.enabled.filled {
  background-color: var(--system-colors-colors-blue);
}

.web-summary-button.symbol-text.large {
  border-radius: 12px;
  gap: 4px;
  padding: 14px 20px;
}

.web-summary-button.symbol.medium {
  height: 34px;
  width: 34px;
}

.web-summary-button.on-material-false.gray {
  background-color: var(--system-colors-fills-tertiary);
}

.web-summary-button.text.small {
  border-radius: 40px;
  gap: 3px;
  padding: 4px 10px;
}

.web-summary-button.enabled.gray {
  background-color: var(--system-colors-fills-tertiary);
}

.web-summary-button.small.symbol-text {
  border-radius: 40px;
  gap: 3px;
  padding: 4px 10px;
}

.web-summary-button.symbol.large {
  height: 50px;
  width: 50px;
}

.web-summary-button.tinted.enabled {
  background-color: var(--system-colors-miscellaneous-button-tinted-fill);
}

.web-summary-button.symbol-text.medium {
  border-radius: 40px;
  gap: 4px;
  padding: 7px 14px;
}

.web-summary-button.text.medium {
  border-radius: 40px;
  gap: 4px;
  padding: 7px 14px;
}

.web-summary-button.symbol.small {
  height: 28px;
  width: 28px;
}

.web-summary-button.text.large {
  border-radius: 12px;
  gap: 4px;
  padding: 14px 20px;
}

.web-summary-button.tinted.on-material-false.disabled {
  background-color: var(--system-colors-fills-tertiary);
}

.web-summary-button.on-material-false.disabled.filled {
  background-color: var(--system-colors-fills-tertiary);
}

.web-summary-button.tinted.on-material-true.disabled {
  background-color: var(--system-colors-miscellaneous-button-disabeld-BG);
}

.web-summary-button.on-material-true.disabled.filled {
  background-color: var(--system-colors-miscellaneous-button-disabeld-BG);
}

.web-summary-button.on-material-true.gray.disabled {
  background-color: var(--system-colors-miscellaneous-button-disabeld-BG);
}

.web-summary-button.large .text-wrapper {
  font-family: var(--body-regular-font-family);
