/* Frame Style */
.web-summary-frame {
  height: 493px;
  position: relative;
  width: 499px;
}

.web-summary-frame .div-wrapper {
  align-items: center;
  background-color: #ffffff;
  border-radius: 3px;
  box-shadow: -2px 2px 11.3px #00000040;
  display: flex;
  gap: 10px;
  height: 493px;
  justify-content: center;
  padding: 0px 42px;
  position: relative;
  width: 499px;
}

.web-summary-frame .div {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 20px;
  margin-left: -17px;
  margin-right: -17px;
  padding: 61px 0px;
  position: relative;
}

.web-summary-frame .p {
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  color: #267bf1;
  display: -webkit-box;
  font-family: "Inter", Helvetica;
  font-size: 34px;
  font-weight: 600;
  height: 88px;
  letter-spacing: 0;
  line-height: 44px;
  margin-top: -1px;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-overflow: ellipsis;
  width: 449px;
}

.web-summary-frame .element {
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 9;
  color: #000000;
  display: -webkit-box;
  font-family: "Inter", Helvetica;
  font-size: 18px;
  font-weight: 400;
  height: 218px;
  letter-spacing: 0;
  line-height: 24px;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-overflow: ellipsis;
  width: 425px;
}

.web-summary-frame .element-2 {
  color: #000000;
  font-family: "Inter", Helvetica;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 22px;
  position: relative;
  text-align: center;
  width: 308px;
}

.web-summary-frame .text-wrapper-6 {
  color: #267bf1;
  font-family: "Inter", Helvetica;
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 44px;
  margin-top: -1px;
  position: relative;
  text-align: center;
  width: 358px;
}

.web-summary-frame .frame-3 {
  background-color: #ffffff;
  box-shadow: -2px 2px 11.3px #00000040;
  height: 347px;
  width: 347px;

  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  position: relative;
}

/* ReturnIcon */
.outline-interface {
  height: 40px;
  left: 0;
  position: absolute;
  top: 0;
  width: 40px;
}
